import React, {Component} from 'react'
import {NavLink,withRouter} from "react-router-dom"
import './nav.css'

class Mynav extends Component{
    constructor(props) {
        super(props);
        this.clickHandle = this.clickHandle.bind(this);
        this.state = {
            isOpen:false
        };
        this.props.history.listen(route => {
            this.setState({
                isOpen:false
            });
        })
    }

    clickHandle () {
        this.setState({
            isOpen:!this.state.isOpen
        });
    }
    render () {
        return (
            <div>
                <nav className="nav">
                    <input type="checkbox" className="nav__cb" id="menu-cb" checked={this.state.isOpen} onChange={()=> this.clickHandle() }/>
                        <div className="nav__content">
                            <ul className="nav__items">
                                <li className="nav__item"><NavLink to='/'><span className="nav__item-text"> 主页 </span></NavLink></li>
                                <li className="nav__item"><NavLink to='/resume'><span className="nav__item-text"> 简历 </span></NavLink></li>
                                <li className="nav__item"><NavLink to='/life'><span className="nav__item-text"> 生活 </span></NavLink></li>
                                <li className="nav__item"><NavLink to='/future'><span className="nav__item-text"> 展望 </span></NavLink></li>
                            </ul>
                        </div>
                        <label className="nav__btn" htmlFor="menu-cb"/>
                </nav>
            </div>
        )
    }
}
export default withRouter(Mynav)