import React, {Component} from 'react'
import './clover.css'
export default class Clover extends Component{
    constructor(props){
        super(props);
        this.state = {
            one:false,
            two:false,
            three:false,
            four:false
        };
        this.clickHandle = this.clickHandle.bind(this);
    }
    clickHandle (cl,e){
        e.preventDefault();
        this.setState({
            one:false,
            two:false,
            three:false,
            four:false
        });
        this.setState({
            [cl]:true
        });
    }
    render () {
        return (
            <main>
                <div className={this.state.one? 'ones':'one'} onClick={(e)=>this.clickHandle('one',e)}>医疗</div>
                <div className={this.state.two? 'twos':'two'} onClick={(e)=>this.clickHandle('two',e)}>饮食</div>
                <div className={this.state.three? 'threes':'three'} onClick={(e)=>this.clickHandle('three',e)}>运动</div>
                <div className={this.state.four? 'fours':'four'} onClick={(e)=>this.clickHandle('four',e)}>消费</div>
            </main>
        )
    }
}